
<style scoped>
.header_center{
  display: flex;
  align-content: center;
  justify-content: center;
  border-style:solid;
  border-width:1px; 
}
.shadow-button {
   margin: 10px;
   box-shadow: 0 3px #999;
}
.button-group{
  display: flex;
  align-content: right;
  justify-content: right;
  margin: 100px;
}
.cleanning_img {
  display: inline;
  width: 45px;
  height: 40px;
  margin-top: 20px;
  margin-bottom: 10px;
}
.el-button {
  margin-top: 6px;
  width: 150px;
}
.el-select {
  margin-top: 6px;
  width: 150px;
  margin-bottom: 40px;
}
.el-row-col {
  line-height: 60px;
  font-size: 20px;
  font-weight: 600;
  /* color: red; */
}
.new-block {
  border: solid;
  background-color: rgb(209, 252, 194);
  margin:5px;
  height: 100%;
}

</style>


<template>
  <el-row >
    <div  class = "header_center">
        <b class = "el-row-col">Suggestions</b>
      </div>
        <el-card>
        <div class="new-block ">
          <b>Suggestion</b>
          <br/>
            
          Transform all header formula into
          
          <el-select v-model="value" placeholder="Select">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>    
          </el-select>
          <el-button class="shadow-button"  type="success" >Preview</el-button>
          <el-button class="shadow-button"  type="primary" >Add</el-button>
        </div>
        </el-card>

  </el-row>
</template>

<script>
import axios from "axios";
import endpoint from "../util/endpoint";
import Header from './Header.vue';

export default {
  components: { Header },
  name: "Suggestion",
  data() {
    return {
      value: "",
      isWholeDF: false,
      parashow: "",
       options: [{
          value: 'name_of_col',
          label: 'name_of_col'
        }, {
          value: 'name_of_row',
          label: 'name_of_row'
        }, {
          value: 'parameter_name',
          label: 'parameter_name'
        }],
    };
  },
  methods: {
    clickDataFrame: function () {
      this.isWholeDF = true;
      this.parashow = new Date().getTime();
      this.value = "";
    },
  },
  watch: {
    value: function (nval, oval) {
      let _this = this;
      if (!(nval == "" && _this.isWholeDF)) {
        _this.isWholeDF = false;
        axios
          .post(endpoint.API_URL + "getFunctionParams", {
            clean_func: nval,
          })
          .then(function (res) {
            let tableColumns = res.data.tableColumns;
            let paramDic = res.data.paramDic;
            let paramDefault = res.data.paramDefault;

            _this.$emit(
              "optionsValue",
              nval,
              tableColumns,
              paramDic,
              paramDefault
            );
          });
      }
    },
    parashow: function (nval, oval) {
      this.$emit("parashow", nval);
    },
  },
};
</script>



