
<style scoped>
.header_center{
  display: flex;
  align-content: center;
  justify-content: center;
  border-style:solid;
  border-width:1px; 
}

.button_layout{
  display: flex;
  align-content: right;
  justify-content: right;
  border-style:solid;
  border-width:1px; 
}
.cleanning_img {
  display: inline;
  width: 45px;
  height: 40px;
  margin-top: 20px;
  margin-bottom: 10px;
}
.el-button {
  margin-top: 6px;
  width: 150px;
  
}
.el-select {
  margin-top: 6px;
  width: 150px;
  margin-bottom: 40px;
}
.el-row-col {
  line-height: 60px;
  font-size: 20px;
  font-weight: 600;
  margin: 10px;

  /* color: red; */
}
.new-block {
  border: solid;
  background-color:blanchedalmond;
  margin: 5px;
  height: 50px;
}

.shadow-button {
   margin: 10px;
   box-shadow: 0 3px #999;
}
</style>


<template>
  <el-row >

    <div  class = "header_center">
      <b class = "el-row-col">Recipe</b> 
   </div>
    <div  class = "header_center">
      <el-button class="shadow-button"  type="success" >Apply</el-button>
      <el-button class="shadow-button"  type="primary" >Export</el-button>
   </div>


    <el-card>
      <el-button type="warning" round>Rollback</el-button>
      <br/>
      <br/>
        <div v-for="o in 4" :key="o" class="new-block ">
            {{'Recipe ' + o }}
        </div>
    </el-card>
   
      
      <!-- <el-button @click="clickDataFrame">Whole DF</el-button><br /> -->
      <!-- <el-select v-model="value" placeholder="Single Col">
        <el-option
          v-for="(value, label) in options"
          :key="label"
          :label="value"
          :value="label"
        >
        </el-option>
      </el-select> -->

  </el-row>
</template>

<script>
import axios from "axios";
import endpoint from "../util/endpoint";
import Header from './Header.vue';

export default {
  components: { Header },
  name: "Recipe",
  data() {
    return {
    
      value: "",
      isWholeDF: false,
      parashow: "",
    };
  },
  methods: {
    clickDataFrame: function () {
      this.isWholeDF = true;
      this.parashow = new Date().getTime();
      this.value = "";
    },
  },
  watch: {
    value: function (nval, oval) {
      let _this = this;
      if (!(nval == "" && _this.isWholeDF)) {
        _this.isWholeDF = false;
        axios
          .post(endpoint.API_URL + "getFunctionParams", {
            clean_func: nval,
          })
          .then(function (res) {
            let tableColumns = res.data.tableColumns;
            let paramDic = res.data.paramDic;
            let paramDefault = res.data.paramDefault;

            _this.$emit(
              "optionsValue",
              nval,
              tableColumns,
              paramDic,
              paramDefault
            );
          });
      }
    },
    parashow: function (nval, oval) {
      this.$emit("parashow", nval);
    },
  },
};
</script>



