<template>
<div>
  <el-table
    :data="tableData"
    style="width: 100%;"
    :fit="true"
    :default-sort="{ prop: 'date', order: 'descending' }"
    :cell-style = "setColColor"
  >
  <el-button @click="setColColor"> abc </el-button>
    <el-table-column
      sortable
      show-overflow-tooltip
      v-for="(column, index) in tableColumns"
      v-bind:prop="column.colName"
      v-bind:label="column.colLabel"
      v-bind:key="index"
    >
    </el-table-column>
    

  </el-table>
  
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "MainTable",
  data() {
    return {
      tableData: [],
      tableColumns: {},
    };
  },
  created() {
    axios
      .get("/getInitSheet")
      .then((res) => {
        this.tableData = res.data["tableData"];
        this.tableColumns = res.data["tableColumns"];
      })
      .catch((e) => {
        console.log(e);
      });
      
  },
  setCellColor({row,column,rowIndex,columnIndex}){
        if(columnIndex===3){
            return 'color:#239ce6;';
        }
  },
  

};
</script>
<style scoped>
</style>



